const ADD_TO_ORDERS = "ADD_TO_ORDERS";

const defaultState ={
    orders: [], 
    payment_info: []   
}

export const orderReducer = (state=defaultState, action) => {
    switch(action.type){
        case ADD_TO_ORDERS: return {...state, orders: [...state.orders, action.payload],
            payment_info: [...state.payment_info, 
                {payment_way: localStorage['payment_way'], 
                user: JSON.parse(localStorage['user']), 
                delivery_date:JSON.parse(localStorage['deliverydate']), 
                order_number: localStorage['ordernumber']} 
            ]
        };
        default:return state;
    }
}

export const addToOrdersAction = (payload) => {
    return {type: ADD_TO_ORDERS, payload};
};

