import React from 'react';
import { useSelector } from 'react-redux';
import SubHeaderComponent from "../../../components/subheader-component/SubHeaderComponent";
import HeaderIntroComponent from '../../../components/header_intro-component/HeaderIntroComponent';
import ProfileComponent from "../../../components/profile-page-components/profile-component/ProfileComponent";
import MyWishcomponent from '../../../components/profile-page-components/mywish-component/MyWishcomponent';
import GalleryComponent from '../../../components/gallery-component/GalleryComponent';
import './profile-wishes.scss';

export default function ProfileWishesPage() {

	const {viewed} = useSelector(state => state.cardsReducer);

  return (
	<div className='profile-wishes'>
		<SubHeaderComponent />
      	<HeaderIntroComponent/>
		<section className="profile-wishes__body">
			<img src="../../assets/Depositphotos_127946766_XL-PhotoRoom.png-PhotoRoom.png" alt="" className="profile-wishes--decor"/>        
			<img src="../../assets/shutterstock_pink-blush-PhotoRoom.png-PhotoRoom.png" alt="" className="profile-wishes--decor2"/>       
			<div className='profile-wishes__block'>
				<ProfileComponent/>
				<div className='profile-wishes-components'>
	          		<MyWishcomponent/>
	            	{viewed.length > 0 && (
						<div className='profile-wishes-components-gallery'>                  
							<GalleryComponent/>                 
						</div>
					)}					
	        	</div>
			</div>
      	</section>
	</div>
  )
}
