import React from 'react';
import { useSelector, useDispatch} from 'react-redux';
import { bodyCare, skinCare, hairCare, spa, makeup } from '../../../utils/Utils.js';
import { getSearchedBodyCareCardsAction, 
         getSearchedHairCareCardsAction, 
         getSearchedSkinCareCardsAction, 
         getSearchedSpaCardsAction,
         getSearchedMakeupCardsAction, 
         getSearchedCardsAction,
        setPageQuantityAction, 
        changeCategoryAction,       
    } from '../../../store/cardsReducer.js';
import { styles } from '../../../styles/styles';
import './shopCategory.scss';

export default function ShopCategoryComponent() {
    const dispatch = useDispatch();
    const {cards, shopCards} = useSelector(state => state.cardsReducer);     

    function setBodyCare(){dispatch(getSearchedBodyCareCardsAction()); dispatch(setPageQuantityAction()); dispatch(changeCategoryAction('bodycare'));};
    function setHairCare(){dispatch(getSearchedHairCareCardsAction()); dispatch(setPageQuantityAction()); dispatch(changeCategoryAction('haircare'));};
    function setSkinCare(){dispatch(getSearchedSkinCareCardsAction()); dispatch(setPageQuantityAction()); dispatch(changeCategoryAction('skincare'));};
    function setSPA(){dispatch(getSearchedSpaCardsAction()); dispatch(setPageQuantityAction()); dispatch(changeCategoryAction('spa'));};
    function setMakeUp(){dispatch(getSearchedMakeupCardsAction()); dispatch(setPageQuantityAction()); dispatch(changeCategoryAction('makeup'));};
    function setAll(){dispatch(getSearchedCardsAction(cards)); dispatch(setPageQuantityAction()); dispatch(changeCategoryAction('all'));};

  return (
    <div className='shop__view-categories'>
        <h3 className='shop__view-categories--title' style={styles.titleStyle}>Categories</h3>                
        <p className='shop__view-categories--item' style={styles.headerText} onClick={setBodyCare}>
            Body Care <span className='category__items-quantity'>({bodyCare(shopCards)})</span>
        </p>
        <p className='shop__view-categories--item' style={styles.headerText} onClick={setHairCare}>
            Hair care <span className='category__items-quantity'>({hairCare(shopCards)})</span>
        </p>
        <p className='shop__view-categories--item' style={styles.headerText} onClick={setSkinCare}>
            Skin care <span className='category__items-quantity'>({skinCare(shopCards)})</span>
        </p>
        <p className='shop__view-categories--item' style={styles.headerText} onClick={setSPA}>
            SPA <span className='category__items-quantity'>({spa(shopCards)})</span>
        </p>
        <p className='shop__view-categories--item' style={styles.headerText} onClick={setMakeUp}>
            Make up <span className='category__items-quantity'>({makeup(shopCards)})</span>
        </p>                
        <p className='shop__view-categories--item' style={styles.headerText} onClick={setAll}>
            All <span className='category__items-quantity'>({shopCards.length})</span></p>                
    </div>
  )
}
