import React from 'react';
import { useSelector } from 'react-redux';
import SubHeaderComponent from "../../../components/subheader-component/SubHeaderComponent";
import HeaderIntroComponent from '../../../components/header_intro-component/HeaderIntroComponent';
import ProfileComponent from "../../../components/profile-page-components/profile-component/ProfileComponent";
import MyInfoComponent from '../../../components/profile-page-components/myinfo-component/MyInfoComponent';
import GalleryComponent from '../../../components/gallery-component/GalleryComponent';
import './profile-info.scss';



export default function ProfileInfoPage() {

	const {viewed} = useSelector(state => state.cardsReducer);

  return (
	<div className='profile-info'>
		<SubHeaderComponent />
      	<HeaderIntroComponent/>
		<section className="profile-info__body">
			<img src="../../assets/Depositphotos_127946766_XL-PhotoRoom.png-PhotoRoom.png" alt="" className="profile__info--decor"/>        
			<img src="../../assets/shutterstock_pink-blush-PhotoRoom.png-PhotoRoom.png" alt="" className="profile__info--decor2"/>       
			<div className='profile__info-block'>
				<div className='profile-info--links'>
					<ProfileComponent/>
				</div>
				<div className='profile__info-components'>
          			<MyInfoComponent /> 
          			<div className='profile__info-components-gallery'>
						{viewed?.length > 0 && (
							<GalleryComponent/>
						)}           
          			</div>          			    
        		</div>
			</div>
      </section>
	</div>
  )
}
