import React from 'react';
import { useSelector } from 'react-redux';
import SubHeaderComponent from "../../../components/subheader-component/SubHeaderComponent";
import HeaderIntroComponent from '../../../components/header_intro-component/HeaderIntroComponent';
import ProfileComponent from "../../../components/profile-page-components/profile-component/ProfileComponent";
import MyOrdersComponent from '../../../components/profile-page-components/myorders-component/MyOrdersComponent';
import GalleryComponent from '../../../components/gallery-component/GalleryComponent';
import './profile-orders.scss';

export default function ProfileOrdersPage() {

	const {viewed} = useSelector(state => state.cardsReducer);

  return (
	<div className='profile-orders'>
		<SubHeaderComponent />
      	<HeaderIntroComponent/>
		<section className="profile-orders__body">
			<img src="../../assets/Depositphotos_127946766_XL-PhotoRoom.png-PhotoRoom.png" alt="" className="profile-orders--decor"/>        
			<img src="../../assets/shutterstock_pink-blush-PhotoRoom.png-PhotoRoom.png" alt="" className="profile-orders--decor2"/>       
			<div className='profile-orders-block'>
				<ProfileComponent/>
				<div className='profile-orders-components'>
          			<MyOrdersComponent/> 
          			<div className='profile-orders-components-gallery'>
						{viewed?.length > 0 && (
							<GalleryComponent/>
						)}           
          			</div>           			       
        		</div>
			</div>
      </section>
	</div>
  )
}
