import React, { useEffect } from "react";
import { BrowserRouter } from "react-router-dom";
import { useDispatch} from 'react-redux';
import AppRouter from "./components/AppRouter";
import HeaderComponent from './components/header-components/header/HeaderComponent';
import FooterComponent from "./components/footer-component/FooterComponent";
import API from './components/API/API.json';
import Testimonials from './components/API/Testimonials.json';
import { getCardsAction, setPageQuantityAction} from "./store/cardsReducer";
import { getTrendsAction } from './store/trendReducer';
import { getTestimonialsAction } from './store/testimonialReducer';
import './App.scss';



function App() {
  const dispatch = useDispatch();   

  function getApi(){  
      dispatch(getCardsAction(API));        
      dispatch(getTrendsAction(API)); 
      dispatch(getTestimonialsAction(Testimonials));  
      dispatch(setPageQuantityAction());       
  } 
  useEffect(() =>  {getApi();});
    

  return (    
    <BrowserRouter>
      <HeaderComponent/>   
      <AppRouter/>
      <FooterComponent/>      
    </BrowserRouter>    
  );
}

export default App;
