import React, {useState} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import NotFound from '../NotFound-component/NotFoundComponent';
import {setProductAction} from '../../store/cardsReducer';
import { setCurrentPageNameAction } from '../../store/headerReducer';
import { setPageAction } from '../../store/pageReducer';
import { getSaleprice } from '../../utils/Utils';
import {styles} from '../../styles/styles';
import './gallery.scss';


export default function GalleryComponent() {

	const dispatch = useDispatch();
  	const {cards, viewed} = useSelector(state => state.cardsReducer); 	
	const [firstCount, setFirstCount] = useState(0);
	const [secondCount, setSecondCount] = useState(1);	

	function getNextGalleryItems(){
		if(viewed.length > 1){
			if(secondCount < viewed.length - 1){
				setFirstCount(firstCount + 1);
				setSecondCount(secondCount + 1);
			}
		}   
    }

  function getPreviousGalleryItems(){
	if(viewed.length > 1){
		if(firstCount > 0){
			setFirstCount(firstCount - 1);
			setSecondCount(secondCount - 1);
		}
	}   
  }

  function setPageInfo(pageName){
	dispatch(setPageAction(pageName));
	localStorage.setItem('pageName', pageName);
  }

  function goToProductCardOnly(card){
    dispatch(setProductAction(card));  
    dispatch(setCurrentPageNameAction('shop'));
	setPageInfo('shop');
	window.scrollTo(0, 450);      
  } 

  return (
	<div>
		{viewed?.length > 0 ? (
		<div className='gallery'>
			{cards.length ? (
	        <div className="gallery-block">         
	          {viewed.length > 0 && (
				<div className="gallery-block__content">
	            <p className="gallery-block__content-logo" style={styles.logoStyle}>Cosmetics</p>
	            <h2 className="gallery-block__content-title" style={styles.titleStyle}>You have viewed</h2>
	            <p className="gallery-block__content-text" style={styles.headerText}>
	              Nourish your skin with toxin-free cosmetic products. With the
	              offers that you cant refuse.
	            </p>
	          	</div>
			  )}
			  {viewed.length > 0 && (		
	          <div className="gallery-block__cards">
	            <div className="gallery-block__cards_btn">
	              <button className={viewed?.length > 1 ? "gallery-block__btns-btn gallery-left-btn--active" : "gallery-block__btns-btn gallery-left-btn"} 
	               style={firstCount >= 1 ? styles.galleryBtnStyle: {backgroundColor: "#FAF9FF", color: '#999999', border: '2px solid #999999'}}
	               onClick={getPreviousGalleryItems}>&#10094;
	               </button>
	            </div>
	            <div className="gallery-block__cards-block">
	              <Link to="/product" className="gallery-block__block-card" style={viewed.length < 2 ? styles.galleryOneItemStyle: styles.galleryTwoItemStyle}
				   onClick={() => goToProductCardOnly(viewed[firstCount])}>                
	                  <div className="gallery-block__card-img">
	                    <img src={viewed[firstCount]?.image} alt="" className="gallery-block__card-image"/>                    
	                  </div>               
						{viewed[firstCount]?.sale > 0 ? (
						    <div className='gallery-block__card-info'>							
								<div className="gallery-block__img-sale" style={styles.headerMessage}>SALE</div>
								{viewed[firstCount]?.new === true && (
									<div className="gallery-block__img-new" style={styles.headerMessage}>NEW</div>
								)}
								<h5 className="gallery-block__card-title" style={ styles.titleStyle}>{viewed[firstCount]?.title.toLowerCase()}</h5>
								<p className="gallery-block__card-text" style={viewed.length < 2 ? styles.galleryOneCardText :styles.headerText}>{viewed[firstCount]?.description}</p>
								{viewed[firstCount]?.sale > 0 ? (
								<div className="gallery-block__card-cost">
									<span className="gallery-block__card-old-price" style={styles.headerText}>{viewed[firstCount]?.price}$</span>
									<span className="gallery-block__card-price" style={styles.headerMessage}>{getSaleprice(viewed[firstCount])}$</span>
								</div>
								) : (
								<div className="gallery-block__card-cost">
									<span className="gallery-block__card-price" style={styles.headerMessage}>{viewed[firstCount]?.price}$</span>
								</div>
								)}
							</div>
							) : (
							<div className='gallery-block__card-info'>
								{viewed[firstCount]?.new === true && (
									<div className="gallery-block__img-new new-gallery" style={styles.headerMessage}>NEW</div>
								)}
								<h5 className="gallery-block__card-title" style={styles.titleStyle}>{viewed[firstCount]?.title.toLowerCase()}</h5>
								<p className="gallery-block__card-text" style={viewed.length < 2 ? styles.galleryOneCardText :styles.headerText}>{viewed[firstCount]?.description}</p>
							{viewed[firstCount]?.sale > 0 ? (
							<div className="gallery-block__card-cost">
								<span className="gallery-block__card-old-price" style={styles.headerText}>{viewed[firstCount]?.price}$</span>
								<span className="gallery-block__card-price" style={styles.headerMessage}>{getSaleprice(viewed[firstCount])}$</span>
							</div>
							) : (
							<div className="gallery-block__card-cost">
								<span className="gallery-block__card-price" style={styles.headerMessage}>{viewed[firstCount]?.price}$</span>
							</div>
							)}
						</div>
					)}
	              </Link>
	              {viewed.length > 1 && (
					<Link to="/product" className="gallery-block__block-card" style={viewed.length > 1 ? styles.galleryTwoItemStyle: styles.galleryOneItemStyle}
					 onClick={() => goToProductCardOnly(viewed[secondCount])} >
						<div className="gallery-block__card-img">
	                    <img src={viewed[secondCount]?.image} alt="" className="gallery-block__card-image"/>                    
	                  </div>               
						{viewed[secondCount]?.sale > 0 ? (
						    <div className='gallery-block__card-info'>							
								<div className="gallery-block__img-sale" style={styles.headerMessage}>SALE</div>
								{viewed[secondCount]?.new === true && (
									<div className="gallery-block__img-new" style={styles.headerMessage}>NEW</div>
								)}
								<h5 className="gallery-block__card-title"style={styles.titleStyle}>{viewed[secondCount]?.title.toLowerCase()}</h5>
								<p className="gallery-block__card-text" style={styles.headerText}>{viewed[secondCount]?.description}</p>
								{viewed[secondCount]?.sale > 0 ? (
								<div className="gallery-block__card-cost">
									<span className="gallery-block__card-old-price" style={styles.headerText}>{viewed[secondCount]?.price}$</span>
									<span className="gallery-block__card-price" style={styles.headerMessage}>{getSaleprice(viewed[secondCount])}$</span>
								</div>
								) : (
								<div className="gallery-block__card-cost">
									<span className="gallery-block__card-price" style={styles.headerMessage}>{viewed[secondCount]?.price}$</span>
								</div>
								)}
							</div>
							) : (
							<div className='gallery-block__card-info'>
								{viewed[secondCount]?.new === true && (
									<div className="gallery-block__img-new new-gallery" style={styles.headerMessage}>NEW</div>
								)}
								<h5 className="gallery-block__card-title" style={styles.titleStyle}>{viewed[secondCount]?.title.toLowerCase()}</h5>
								<p className="gallery-block__card-text" style={styles.headerText}>{viewed[secondCount]?.description}</p>
							{viewed[secondCount]?.sale > 0 ? (
							<div className="gallery-block__card-cost">
								<span className="gallery-block__card-old-price" style={styles.headerText}>{viewed[secondCount]?.price}$</span>
								<span className="gallery-block__card-price" style={styles.headerMessage}>{getSaleprice(viewed[secondCount])}$</span>
							</div>
							) : (
							<div className="gallery-block__card-cost">
								<span className="gallery-block__card-price" style={styles.headerMessage}>{viewed[secondCount]?.price}$</span>
							</div>
							)}
						</div>
					)}					
					</Link>
				  )}              
	            </div>
	            <div className="gallery-block__cards_btn">
	              	<button className={viewed?.length > 1 ? "gallery-block__btns-btn gallery-right-btn--active" : "gallery-block__btns-btn gallery-right-btn"} 
						style={secondCount < viewed.length - 1 ? styles.galleryBtnStyle: {backgroundColor: "#FAF9FF", color: '#999999', border: '2px solid #999999'}}
						onClick={getNextGalleryItems}>&#10095;
					</button>
	            </div>
	          </div>
			  )}		  
	        </div>
	      ) : (
	        <NotFound />
	      )}
		</div>
		):(
			<div></div>
		)}
	</div>
  )
}
